/* PrismJS 1.16.0
https://prismjs.com/download.html#themes=prism&languages=markup+css+clike+javascript+jsx */
/**
 * prism.js default theme for JavaScript, CSS and HTML
 * Based on dabblet (http://dabblet.com)
 * @author Lea Verou
 */

code[class*="language-"],
pre[class*="language-"] {
	color: black;
	background: none;
	text-shadow: 0 1px white;
	font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
	font-size: 1em;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.7;

	-moz-tab-size: 4;
	tab-size: 4;

	-webkit-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
}

pre[class*="language-"]::selection, pre[class*="language-"] ::selection,
code[class*="language-"]::selection, code[class*="language-"] ::selection {
	text-shadow: none;
	background: #b3d4fc;
}

@media print {
	code[class*="language-"],
	pre[class*="language-"] {
		text-shadow: none;
	}
}

/* Code blocks */
pre[class*="language-"] {
	overflow: auto;
	border-radius: 5px;
	margin: 20px;
	box-shadow: 0 2px 18px -1px #1112;
  padding: 20px;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
	background: #fefefe;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
	padding: .1em;
	border-radius: 3px;
	white-space: normal;
}

.token.punctuation {
	color: #333;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.script-punctuation,
.token.cdata {
	color: gray;
	font-style: italic;
}

.namespace {
	opacity: .7;
}

.token.property,
.token.tag,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
	color: rgb(204, 58, 89);
}

.token.selector,
.token.attr-name,
.token.char,
.token.builtin,
.token.inserted {
	color: rgb(149, 211, 25);
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
	color: #f0d041;
	background: hsla(0, 0%, 100%, .5);
}

.token.atrule,
.token.attr-value,
.token.keyword {
	color: rgb(25, 166, 226);
}

.token.function,
.token.class-name {
	color: rgb(51, 181, 236);
}

.token.regex,
.token.important,
.token.boolean,
.token.string,
.token.variable {
	color: #e90;
}

.token.important,
.token.class-name,
.token.bold {
	font-weight: bold;
}

.token.italic {
	font-style: italic;
}

.token.entity {
	cursor: help;
}


